/* eslint-disable */
import * as dd from 'dingtalk-jsapi';
import qs from 'qs'
import Axios from 'axios';
let config={
    corpId:"ding989f59cf138502f135c2f4657eb6378f",
    domain:"http://localhost:8080"
}
export default {
    confirm(message, title, yLabel, fLabel) {
        return new Promise((resolve, reject) => {
            dd.ready(function () {
                dd.device.notification.confirm({
                    message: message,
                    title: title,
                    buttonLabels: [yLabel, fLabel],
                    onSuccess: function (result) {
                        //onSuccess将在点击button之后回调
                        /*
                        {
                            buttonIndex: 0 //被点击按钮的索引值，Number类型，从0开始
                        }
                        */
                        if (result.buttonIndex == 0) {
                            resolve(result);
                        } else {
                            reject(result);
                        }
                    },
                    onFail: function (err) {
                        reject(err);
                    }
                });
            });

        });

    },
    login(){
        return new Promise((resolve, reject) => {
            dd.ready(function () {
                dd.runtime.permission.requestAuthCode({
                    corpId: config.corpId, // 企业id
                    onSuccess: function (info) {
                            let code = info.code // 通过该免登授权码可以获取用户身份
                            Axios(
                            {
                                method: 'post',
                                url: config.domain+"/login",
                                data: qs.stringify({"authCode":code}),
                                withCredentials: true,
                                headers: {            
                                    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
                                }
                            }
                            ).then(
                                (data)=>{
                                    resolve(data);
                                }
                            );
                    },
                    onFail:function(err){
                       reject(err);
                    }
                });
            });

        });
    },
    getDept(pId){
        Axios({
            method: 'post',
            data:qs.stringify({"pId":pId}),
            url: config.domain+"/getDept",
            withCredentials: true,
            headers: {            
                'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
            }
        }                    
        ).then(
            (data)=>{
                window.console.log(data);
            }
        );
    }
}